import { useState, useRef, useEffect } from 'react'
import './Form.css'
import language from './Language'


function Form() {
    let quyosh = useRef()
    let oy = useRef()
    const rootRef = useRef(null)
    let text = useRef()
    let selectref = useRef()
    let optionref = useRef()
    let inputname = useRef()
    let formtext = useRef()
    let inputpassword = useRef()
    let [btndisabled, setDisabled] = useState()
    let formbtn = useRef()
    let nametogri = useRef()
    let namenotogri = useRef()
    let passwordtogri = useRef()
    let passwordnotogri = useRef()


  return (
      <>
      <div ref={rootRef} className={`tyomniy ${window.localStorage.getItem('__local__') === 'black' ? 'qora' : ''}`}>
      <h1 className={`yoruq ${window.localStorage.getItem('__local__') === 'black' ? 'hide' : ''}`} ref={quyosh} onClick={(e) => {
          quyosh.current.style.display = 'none';
          oy.current.style.display = 'block';
          rootRef.current.classList = 'tyomniy qora'
          window.localStorage.setItem('__local__', 'black')
        }}>üåû</h1>
      <h1 className={`qorongu ${window.localStorage.getItem('__local__') === 'black' ? 'show' : ''}`} ref={oy} onClick={(e) => {
          quyosh.current.style.display = 'block';
          oy.current.style.display = 'none'
          rootRef.current.classList = 'tyomniy'
        window.localStorage.setItem('__local__', 'white')
        }}>üåõ</h1>
      </div>
      <select className="select" ref={selectref} onClick={(e) => {
          if(e.target.value === 'uz') {
              text.current.textContent = language.uz.greeting;
          }else if(e.target.value === 'en') {
            text.current.textContent = language.en.greeting;
        }else if(e.target.value === 'ru') {
            text.current.textContent = language.ru.greeting;
        }
      }}>
          <option className="option" value="uz" ref={optionref}>Uz</option>
          <option className="option" value="en" ref={optionref}>En</option>
          <option className="option" value="ru" ref={optionref}>Ru</option>
      </select>
      <div className="text__container">
      <div className="text__div">
      <p className="text" ref={text}>Birinchi inputga ismingizni kiriting, <br/> ismingiz 3 ta va 3 tadan uzunroq bo'lishi kerak, <br /> Ikkinchi inputga esa parolingizni kiriting, <br/> parolingiz 6 ta va 6 tadan ko'p bo'lishi kerak</p>
      </div>
      </div>
      <div className="form__container">
      <div className="form__div">
      <form  className="form"  onSubmit={(e) => {
          e.preventDefault()
              if(inputname.current.value.length === 0 && inputpassword.current.value.length === 0){
                formtext.current.style.display = 'block'
              }else if(inputname.current.value.length > 0 && inputpassword.current.value.length === 0) {
                formtext.current.textContent = 'Enter your password'
                formtext.current.style.display = 'block'
              }else if(inputname.current.value.length === 0 && inputpassword.current.value.length > 0) {
                formtext.current.textContent = 'Enter your name'
                formtext.current.style.display = 'block'
              }
          }}>
          <h2 className="form__title">Form</h2>
          <p className="form__text" ref={formtext}>Enter your name <br/> and password</p>
          <h2 className="nametogri" ref={nametogri}>üëç</h2>
          <h2 className="namenotogri" ref={namenotogri}>üëé</h2>
          <input ref={inputname} type="text" className="inputname" placeholder="Name" onChange={(e) => {
              if(e.target.value.length >= 3){
                nametogri.current.style.display = 'block'
                namenotogri.current.style.display = 'none'
                }else if(e.target.value.length < 3) {
                  namenotogri.current.style.display = 'block'
                  nametogri.current.style.display = 'none'
                }
          }}/>
          <h2 className="passwordtogri" ref={passwordtogri}>üëç</h2>
          <h2 className="passwordnotogri" ref={passwordnotogri}>üëé</h2>
          <input ref={inputpassword} type="password" className="inputpassword" placeholder="password" onChange={(e) => {
              if(e.target.value.length >= 6){
              passwordtogri.current.style.display = 'block'
              passwordnotogri.current.style.display = 'none'
              }else if(e.target.value.length < 6) {
                passwordnotogri.current.style.display = 'block'
                passwordtogri.current.style.display = 'none'
              }
          }}/>
          <button type="submit" className="form__btn">submit</button>
      </form>
      </div>
      </div>
      </>
  )
}

export default Form;